var isLoding = false;
var timer = null;
var timer_ = null;
//邮箱验证
var util = require('../../common/util.js');
var email; //输入的email值
var captcha; //输入的captcha值
import whCaptcha from '../../components/wh-captcha/wh-captcha.vue';
export default {
	components: {
		whCaptcha
	},
	data() {
		return {
			status: false,
			swiperLeft: 0,
			swiperLineWidth: 0,
			captchaLength: 6,
		};
	},
	methods: {
		onBackPress(e) {
			// return true 表示禁止默认返回
			console.log("监听到返回")
			return false
		},

		onEmailInput(event) {
			email = event.target.value;
		},

		onCaptchaInput(event) {
			captcha = event.target.value;
		},

		/**
		 * true: 不匹配, false: 匹配
		 */
		checkUNNCEmail(str) {
			return !RegExp(/^\w+([-+.]\w+)*@nottingham\.edu\.cn+$/).test(str);
		},

		getCaptcha() {
			if (email) {
				// 检测邮箱
				if (util.regEmail(email) || this.checkUNNCEmail(email)) {
					uni.showToast({
						title: '非 UNNC 邮箱地址！',
						icon: 'none'
					});
				} else {
					if (this.$refs.captcha.canSend()) {
						console.log("获取验证码 email=" + email);
						this.$refs.captcha.begin();

						var that = this;
						uni.request({
							url: that.$serverUrl + '/user/getCode',
							method: "POST",
							data: {
								userId: that.getGlobalUserInfo().id,
								email: email,
							},
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: (res) => {
								console.log(res);
							},
						});
					}
				}
			} else {
				uni.showToast({
					title: 'Email不能为空',
					icon: 'none'
				});
			}
		},

		confirmCode() {
			if (captcha) {
				var that = this;
				uni.request({
					url: that.$serverUrl + '/user/confirmCode',
					method: "POST",
					data: {
						userId: that.getGlobalUserInfo().id,
						code: captcha,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.status == 200) {
							console.log("验证邮箱成功，下一步");
							// 进度条运动
							clearInterval(timer_); //清空定时器，防止重复操作
							var that = this;
							timer_ = setInterval(function() {
								if (that.swiperLineWidth >= 100) {
									clearInterval(timer_);
								} else {
									that.swiperLineWidth = that.swiperLineWidth + 2;
									console.log(that.swiperLineWidth);
								}
							}, 20)
							uni.redirectTo({
								url: "../index/index"
							});
						} else {
							console.log("验证失败 " + res.data.msg);
						}
					},
				});
			} else {
				uni.showToast({
					title: '验证码为空',
					icon: 'none'
				});
			}
		},

		/**
		 * 微信小程序登陆
		 */
		getUserInfo(e) {
			// console.log("getting UserInfo...")
			// console.log(e);

			// 加锁
			if (isLoding) {
				return;
			}
			isLoding = true;

			uni.showLoading({
				title: '载入中...',
			});
			setTimeout(() => {
				if (isLoding) {
					isLoding = false // 解锁
					uni.hideLoading();
					uni.showToast({
						title: "网络未知错误",
						icon: "none",
						duration: 1000
					})
				}
			}, 5000); // 延时5s timeout

			var info = e.detail;
			var that = this;
			uni.login({
				success: res_login => {
					// console.log('-------res_login，获取code-------')
					// console.log(res_login);
					// 后端获取openid 并设置用户信息
					uni.request({
						url: that.$serverUrl + '/user/getWxUserInfo',
						method: "POST",
						data: {
							encryptedData: info.encryptedData,
							iv: info.iv,
							code: res_login.code,

							nickname: info.userInfo.nickName,
							faceImg: info.userInfo.avatarUrl
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						success: (res) => {
							// console.log(res)
							if (res.data.status == 200) {
								// 3.获取返回的用户信息
								var finalUser = res.data.data;
								// 4.分割邮箱地址, 重构 user
								finalUser = this.myUser(finalUser);
								// 5.写入缓存
								this.setGlobalUserInfo(finalUser);
								console.log(finalUser);
								// 转场动画
								// 需计算转场时间以保证进度条和块内容运动一致
								// 当前运动时间为 500 ms

								// 块运动
								clearInterval(timer); //清空定时器，防止重复操作
								var that = this;
								timer = setInterval(function() {
									if (that.swiperLeft <= -100) {
										clearInterval(timer);
									} else {
										that.swiperLeft = that.swiperLeft - 2;
										console.log(that.swiperLeft);
									}
								}, 10)
								// 进度条运动
								clearInterval(timer_); //清空定时器，防止重复操作
								var that = this;
								timer_ = setInterval(function() {
									if (that.swiperLineWidth >= 50) {
										clearInterval(timer_);
									} else {
										that.swiperLineWidth = that.swiperLineWidth + 2;
										console.log(that.swiperLineWidth);
									}
								}, 20)

								isLoding = false;
								uni.hideLoading();
							}
						}
					});
				}, // end of login success
				fail: (res) => {
					isLoding = false;
					uni.hideLoading();
					uni.showToast({
						title: "login fail",
						icon: "none",
						duration: 1000
					})
				}

			});
		},

		changestatus() {
			this.status = !this.status;
		},
		
		toUserDeal(){
			uni.navigateTo({
				url: "../userDeal/userDeal"
			});
		}
	}
};
